body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.navBar {
  background-color: #5ac0b8;
  height: 80px;
  width: 100%;
  padding: 10px;
  display: inline-flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.blogTitle{
  color: white;
  font-weight: 400;
  font-size: 27px;
  margin-top: 25px;
  margin-left: 50px;
}
.statusBar{
  text-align: right;
  margin-top: 30px;
  margin-right: 20px;
}
a:-webkit-any-link {
  text-decoration: none;
  color: #505964;
}
span > a {
  margin-right: 15px;
}
.postTitleList {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
}
.postTitle {
  padding:15px 25px;
  width: 240px;
}
img{
  border-radius: 5px;
}
.content {
  margin: 30px auto;
}
.quote{
  width: fit-content;
  margin:auto;
}
.randomSentence {
  margin-top: 120px;
  width: 600px;
}
.sentenceAuthor {
  margin: 20px auto;
  text-align: right;
}
.postWrap {
  width: 100%;
  display: inline-flex;
  justify-content: center;
}
.singlePost{
  display: inline-flex;
  width: fit-content;
  margin: 0 50px;
}
.word{
  width: fit-content;
  margin-right: 100px;
}
h1{
  margin-top: 30px;
  margin-bottom: 5px;
}
.postAuthor{
  color: 	#AAAAAA;
  font-size: 16px;
  margin-bottom: 5px;
}